import React from 'react';
import data from './NewPost.module.css';
import { reduxForm, Field } from 'redux-form';
import {required} from '../../../../../Utils/Validators/Validators';


//const required = value => (value || typeof value === 'number' ? undefined : 'Required')

const NewPost = (props) => {

  return (

    <div className={data.item}>

      <form onSubmit={props.handleSubmit}>

        <Field className={data.textarea} name={"newPostMessage"} 
        validate={[required]}
              placeholder="Что у вас нового?" component="textarea" />
        
        <div className={data.button} align='right'>

          <button >
            Опубликовать
          </button>

        </div>

      </form>

    </div>

  )
}


let NewPostReduxForm = reduxForm({ form: 'NewPostRedux' })(NewPost)

export default NewPostReduxForm;
