
const ADD_MESSAGE = 'ADD-MESSAGE';

let initialState = {

    DialogList: [
        { id: 1, name: 'Марина' },
        { id: 2, name: 'Юля' },
        { id: 3, name: 'Слава' },
        { id: 4, name: 'Бабушка Таня' }
    ],

    MessageList: [
        { note: 'Привет, Марина' },
        { note: 'Привет, Юля' },
        { note: 'Привет, Слава' }
    ],

}

const MessagesPageReducer = (state=initialState, action) => {

    switch (action.type) {

        case ADD_MESSAGE: 
           
            return { ...state, MessageList: [...state.MessageList, 
                {note: action.newMessage}]
             }
            
        default:
            return state;
    }
}


export let addMessage = (newMessage) => ({type: ADD_MESSAGE, newMessage})

export default MessagesPageReducer;