import React from 'react';
import data from './ProfileInfo.module.css';
import Preloader from '../../Common/Preloader/Preloader';
import userPhoto from '../../../../Assets/Images/user.jpg';
import hired from '../../../../Assets/Images/hired_200.png';
import looking_for_job from '../../../../Assets/Images/looking_for_job.png';
import ProfileStatus from './ProfileStatus';


const ProfileInfo = (props) => {
  
  if (!props.profile) {
    return <Preloader />
  }

  return (

    <div className={data.main}>
      <div >
        <img src={props.profile.photos.large != null ?
          props.profile.photos.large : userPhoto} />
      </div>


      <div className={data.info_wrapper}>
        <div className={data.info}>
          <div className={data.name}>
            {props.profile.fullName}
          </div>
          <div className={data.status}>




            <ProfileStatus {...props}/>




          </div>
        </div>
        <div className={data.profile_info}>
          <div className={data.contacts}>
            <div className={data.contacts_header}>
              Контакты:
            </div>
            <div className={data.contact_item}>
              <div>Facebook:</div>
              <div>Website:</div>
              <div>Vk:</div>
              <div>Twitter:</div>
              <div>Instagram:</div>
              <div>Youtube:</div>
              <div>Github:</div>
              <div>MainLink:</div>

            </div>
            <div className={data.contact_link}>
              <div>{props.profile.contacts.facebook != null ? props.profile.contacts.facebook : '-'}</div>
              <div>{props.profile.contacts.website != null ? props.profile.contacts.website : '-'}</div>
              <div>{props.profile.contacts.vk != null ? props.profile.contacts.vk : '-'}</div>
              <div>{props.profile.contacts.twitter != null ? props.profile.contacts.twitter : '-'}</div>
              <div>{props.profile.contacts.instagram != null ? props.profile.contacts.instagram : '-'}</div>
              <div>{props.profile.contacts.youtube != null ? props.profile.contacts.youtube : '-'}</div>
              <div>{props.profile.contacts.github != null ? props.profile.contacts.github : '-'}</div>
              <div>{props.profile.contacts.mainlink != null ? props.profile.contacts.mainlink : '-'}</div>
            </div>

          </div>
          <div className={data.job_info}>

            <div className={data.job_header}>
              {props.profile.lookingForAJob=true ? 'Ищу работу' : 'Работаю'}
            </div>
            <div>
              <img src={props.profile.lookingForAJob = true ?
                looking_for_job : hired} />
            </div>
            <div className={data.job_description}>
              {props.profile.lookingForAJobDescription}
            </div>


          </div>

        </div>
      </div>

    </div>
  );
}

export default ProfileInfo;
