import React from 'react';
import NewPostReduxForm from './NewPostReduxForm';



const NewPostContainer = (props) => {

  let AddPost = (values) => {
    
    props.addPost(values.newPostMessage);
    
  }

  return (

    <div>
      <NewPostReduxForm onSubmit={AddPost}/> 
    </div>
  )
}

export default NewPostContainer;
