import React from 'react';
import s from './Messages.module.css';
import DialogList from './DialogList/DialogList';
import Dialog from './Dialog/Dialog';
import MessagesReduxForm from './MessagesReduxForm';



const Messages = (props) => {

  let addMessage = (values) => {

    props.addMessage(values.newMessage)
  }

  let correspondence = props.MessagesPage.MessageList.map(m => <Dialog DialogText={m.note} key={m.id} />)

  return (
    <div className={s.messages}>
      <div className={s.dialogList}>
        <DialogList MessagesPage={props.MessagesPage} />
      </div>
      <div className={s.dialogs}>
        
        {correspondence} 

        <MessagesReduxForm onSubmit={addMessage}/>

      </div>

    </div>
  );
}

export default Messages;
