import React from 'react';
import s from './Header.module.css';
import user from '../../Assets/Images/user.jpg';
import { NavLink } from 'react-router-dom';


const Header = (props) => {

  return (

    <header className={s.header}>
      <img src='https://upload.wikimedia.org/wikipedia/commons/thumb/2/2a/ITunes_12.2_logo.png/600px-ITunes_12.2_logo.png' align='left' />

      {(props.isAuth === true) ?

        <div className={s.header_login_field}>
          <a href={'/profile/' + props.id} className={s.headerloginfieldlink} >
            <div className={s.header_login_name}>
              {props.login}

            </div>
            <div>
              <img src={user} />
            </div>

          </a>
          <div className={s.header_login_button}>
            <button onClick={props.logout}>Выйти</button>
          </div>
        </div>

        :

        <div className={s.header_login_field}>
          <div className={s.header_login_button}>
            <button>
              <NavLink to={"/login"}>
                Войти
              </NavLink>


            </button>
          </div>
        </div>
      }


    </header >






  );
}

export default Header;
