import React from 'react';
import cl from './Person.module.css';
import {NavLink} from 'react-router-dom';

/* формирует одну позицию для списка собеседников со ссылкой */

const Person = (props) => {
  return (
      <div>
        <NavLink to={'/messages/'+props.id} className={cl.link}>
          <div className={cl.item}>{props.name}</div>
        </NavLink>
      
    </div>
  );
}

export default Person;
